<template lang="pug">
	div
		header.header
			.header__logo
			.header__container(:class="{header__container_open: open}")
				a.header__link(href="/")
					img(:src="logo")
				ul.header__nav.nav
					li.nav__item
						.nav__link(@click="scroll('#head')") Главная 
					li.nav__item
						.nav__link(@click="scroll('#services')") Услуги
					li.nav__item
						.nav__link(@click="scroll('#about')") О нас
					li.nav__item
						.nav__link(@click="scroll('#why')") Почему мы
					li.nav__item
						.nav__link(@click="scroll('#materials')") Материалы
					li.nav__item
						.nav__link(@click="scroll('#contacts')") Контакты
				.header__contacts
					.contacts__item.item.mail-link
						img.item__img(:src="mail")
						a.item__link(href="mailto:korzaleksandr40@gmail.com") korzaleksandr40@gmail.com
					.contacts__item.item 
						img.item__img(:src="phone")
						a.item__link(href="tel:+380954840295") (095)484 0295
							span Олександр
					.contacts__item.item
						img.item__img(:src="phone")
						a.item__link(href="tel:+380688129537") (068)812 9537
							span Евгений
			.header__burger.burger(:class="{header__burger_close: open}" @click="menuHandler")
				.burger__line
				.burger__line
				.burger__line
</template>

<script>
import logo from "../assets/images/logo.svg";
import phone from "../assets/images/phone.svg";
import mail from "../assets/images/mail.svg"

export default {
	name: 'Header',
	data() {
		return {
			logo: logo,
			mail: mail,
			phone: phone,
			open: false,
		};
	},
	methods: {
		menuHandler() {
			if (window.innerWidth <= 960) {
				this.open = !this.open;
				this.open ? document.querySelector("body").setAttribute("style", "overflow-y: hidden") : document.querySelector("body").setAttribute("style", "overflow-y: visible");
			}
		},
		scroll(id) {
			this.menuHandler();
			window.scrollTo({top: document.querySelector(String(id)).offsetTop, behavior: "smooth"})
		}
	}
}
</script>

<style scoped lang="scss" type="scss">
	.header {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		padding: 25px 0;
		z-index: 2;
		@media(max-width: 960px) {
			padding: 0 0 0 0;
			height: 60px;
			width: 100vw;
			pointer-events: none;
			position: fixed;
			background: #000;
		}
		@media(max-width: 850px) and (orientation: landscape) {
			padding: 0 0 0 0;
			height: 100vh;
			width: 100vw;
			position: fixed;
			height: 60px;
			background: #000;
		}
		
		&__container {
			width: 90%;
			margin: 0 auto;
			text-align: right;
			position: relative;
			@media(max-width: 960px) {
				background: #000;
				width: 100%;
				height: 100%;
				position: fixed;
				top: 0;
				left: 0;
				z-index: 2;
				opacity: 0;
				pointer-events: none;
				transition: opacity .3s ease;
				&_open {
					opacity: 1;
					pointer-events: all;
				}
			}
			@media(max-width: 850px) and (orientation: landscape) {
				background: #000;
				width: 100%;
				height: 100%;
				position: fixed;
				top: 0;
				left: 0;
				z-index: 2;
				opacity: 0;
				pointer-events: none;
				transition: opacity .3s ease;
				&_open {
					opacity: 1;
					pointer-events: all;
				}
			}
		}
		&__logo {
			display: none;
			@media(max-width: 960px) {
				position: absolute;
				top: 50%;
				left: 10px;
				height: 100%;
				width: 40px;
				height: 40px;
				background-image: url(../assets/images/logo.svg);
				transform: translateY(-50%);
				display: block;
				background-size: contain;
				background-repeat: no-repeat;
			}
		}

		&__link {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translate(0, -50%);
			@media(max-width: 960px) {
				display: none;
			}
			@media(max-width: 850px) and (orientation: landscape) {
				display: none;
			}
		}

		&__nav {
			appearance: none;
			display: inline-block;
			vertical-align: middle;
			@media(max-width: 960px) {
				width: 100%;
				padding-top: 50px;
			}
			@media(max-width: 850px) and (orientation: landscape) {
				width: 100%;
				padding: 0;
				text-align: center;
			}
			@media(max-width: 320px) {
				padding-left: 10%;
			}
		}
		&__burger {
			position: absolute;
			top: 20px;
			right: 20px;
			width: 30px;
			height: 20px;
			z-index: 10;
			display: none;
			pointer-events: all;
			@media(max-width: 960px) {
				display: block;
				&_close {
					.burger__line {
						&:nth-child(1) {
							transform: translate(0, 9px) rotate(45deg);
						}
						&:nth-child(2) {
							transform: translate(0, -9px) rotate(-45deg);

						}
						&:nth-child(3) {
							opacity: 0;
						}
					}
				}
			}
			@media(max-width: 850px) and (orientation: landscape) {
				display: block;
				&_close {
					.burger__line {
						&:nth-child(1) {
							transform: translate(0, 9px) rotate(45deg);
						}
						&:nth-child(2) {
							transform: translate(0, -9px) rotate(-45deg);

						}
						&:nth-child(3) {
							opacity: 0;
						}
					}
				}
			}
			
		}
		&__contacts {
			text-align: left;
			padding-left: 40px;
			display: inline-block;
			vertical-align: middle;
			// display: none;
			@media(max-width: 960px) {
				display: block;
			}
			@media(max-width: 850px) and (orientation: landscape) {
				display: block;
			}
			@media(max-width: 320px) {
				padding-left: 10%;
			}
		}
	}

	.nav {
		&__item {
			appearance: none;
			display: inline-block;
			margin: 0 10px;
			@media(max-width: 960px) {
				display: block;
				text-align: left;
				margin: 30px 0;
			}
			@media(max-width: 850px) and (orientation: landscape) {
				display: inline-block;
				text-align: left;
				margin: 30px 5px;
			}
		}

		&__link {
			appearance: none;
			text-decoration: none;
			color: #fff;
			cursor: pointer;
			@media(min-width: 1200px) {
				position: relative;
				&:before {
					content: "";
					position: absolute;
					left: 0;
					bottom: 0;
					width: 100%;
					height: 1px;
					background-color: #fff;
					transform: scaleX(0);
					transition: transform .3s ease;
					z-index: 2;
				}
				&:hover {
					&:before {
						transform: scaleX(1);
					}
				}	
			}
		}
	}
	.burger {
		&__line {
			position: absolute;
			left: 0;
			width: 100%;
			height: 2px;
			top: 50%;
			background-color: #fff;
			&:nth-child(1) {
				top: 0;
			}
			&:nth-child(2) {
				top: calc(100% - 2px);
			}
			&:nth-child(3) {
				top: calc(50% - 1px);
			}
		}
	}
	.contacts {
		&__nav {
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			max-height: 150px;
		}
		&__item {
			font-weight: 800;
			margin-top: 10px;
			span {
				font-weight: 300;
				margin-left: 5px;
			}
		}
	}
	.item {
		img {
			max-width: 23px;
			display: inline-block;
			vertical-align: middle;
		}
		span {
			display: inline-block;
			vertical-align: middle;
			color: #fff;
		}
		a {
			display: inline-block;
			vertical-align: middle;
			color: #fff;
			text-decoration: none;
			margin-left: 15px;
			cursor: pointer;
			transition: opacity .3s ease;
			&:hover {
				opacity: .7;
			}
		}
	}
	.mail-link {
		display: none;
		@media(max-width: 960px) {
			display: block;
		}
	}
</style>
